<template>
  <div>
    <loading :show="isLoading" />
    <vehicle-header 
                noSearch
                title="成绩详情"
                iconName =""/>

    <van-tabs v-show="!isLoading">
      <van-tab v-for="(item,index) in subjects" :key="index" :title="item.km">
        <subject :subjects="subjects" :subData="item" :examJoin="examJoin" :isUnion="isUnion"/>
      </van-tab>  
    </van-tabs>
    <div style="height: 3rem;"/>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Subject from "@/views/Subject.vue"
import Loading from "@/components/Loading.vue"
import Vue from 'vue';
export default {
  components: {
    "vehicle-header": Header,
    "subject":Subject,
    "loading":Loading,
  },
  data () {
    return {
      examData:{examName:''},
      examJoin:0,
      isUnion:false,
      subjects:[],
      isLoading:false,
    };
  },
  mounted(){
    this.isLoading = true;
    this.getSubject();
  },
  watch:{
    '$route'(to,from){
      if(this.$route.query.examGuid != undefined){
        this.isLoading = true;
        this.getSubject();  
      }
    }
  },
  methods: {
    getSubject(){
      this.subjects = [];
      this.examData = this.$route.query;
      let that = this;
      this.zone.querySubjectInfo(this.examData).then(ret =>{
        if(ret.status == 200 && ret.data.status == 200){
          //console.log(ret)
          that.examJoin = ret.data.data.examJoinCount;
          that.isUnion  = ret.data.data.isUnion;
          that.subjects = ret.data.data.subjects;
          let idx=0;
          that.subjects.forEach(el=>{
            that.$set(el,"level",that.zone.calcGradeLevel(el.us,that.examJoin));
            if(el.question){
              let pos = 0;
              el.question.TZs = [];
              for(pos=0;pos<el.question.THs.length;pos++){
                if(el.question.THs[pos].avg){
                  el.question.TZs.push(el.question.THs[pos]);
                  el.question.THs.splice(pos,1);
                  pos--;
                }
              };
            }
            if(el.code >= 0){
              if(el.question != undefined && el.question.asiresponse != undefined){
                that.zone.queryAnswerCard(that.examData,ret.data.data,el).then(ans =>{
                  if(ans.status == 200 && ans.data.status == 200){
                    that.$set(el,"answerCard",ans.data.data);                                               
                  }
                });
              }
              that.zone.queryQuestionKownledge(that.examData,ret.data.data,el).then(kl =>{   
                console.log("-----kl-------")
                console.log(kl);
                if(kl.status == 200 && kl.data.status == 200){
                console.log("-----knowledge-------")                  
                  that.$set(el,"knowledge",kl.data.data.knowledgeItems);   
                  console.log(el.knowledge)                                
                }
              });}
            idx++;
          });
          that.isLoading = false;
        }else{
          if(ret.data.message != undefined)
            that.$toast(ret.data.message);
          else
            that.$toast(ret.message);
          that.$router.push({path:"/login"});
        }

      });
    },
  },
};
</script>



